import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Movie from '@/components/movie/Movie'
import MovieList from '@/components/movie/MovieList'
import MovieDetail from '@/components/movie/MovieDetail'
import MovieMsg from '@/components/movie/MovieMsg'
import Me from '@/components/me/Me'
import Yingyuan from '@/components/movie/Yingyuan'
import maiPiao from '@/components/movie/maiPiao'
import Xiadan from '@/components/movie/Xiadan'
Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:'/movie/movielist'
    //redirect:'/me'
    },
    {
      path:'/movie',
      name:'movie',
      component:Movie,
      children:[
        {
        path:'movielist',
        name:'movielist',
        component:MovieList,
      },{
        path:'yingyuan',
        name:'yingyuan',
        component:Yingyuan,
      }
    ]
    },{
      path:'/moviedetail:movieId',
      name:'moviedetail',
      component:MovieDetail,
    },{
      path:'/moviemsg:Msg',
      name:'moviemsg',
      component:MovieMsg,
    },{
      path:'/me',
      name:'me',
      component:Me,
    },{
      path:'/maipiao:maiPiao',
      name:'maipiao',
      component:maiPiao,
    },{
      path:'/xiadan:XD',
      name:'xiadan',
      component:Xiadan,
    },
      
  ]
})



