<template>
    <div>
   <div class='tou'>
  <span @click='$router.go(-1)' style="font-family: '';font-size:0.7rem;">&lt; </span>
<p class='p' style='font-size:16px; text-align: center;  width:100%;'>{{MPMsg.title}}</p>
   </div>
<article class='MP1'>
    <dl>
        <dt>{{MPMsg.title}}</dt>
        <dd>{{MPMsg.dz}}</dd>
    </dl>
    <div><img src="data:image/png;base64," /></div>


</article>
<article class='MP2'>
<div class='mp2'>
    <dl @click='mp2(two)' v-for='two in movieListTwo' :key='two.id'>
        <dt> <img :src="two.img"/></dt>
        <dd>{{two.want}}</dd>
    </dl>
</div>
</article>
<article class='MP3'>
    <p>{{title}} <span>{{pf}}</span></p>
    <p>{{sj}}|{{jq}}|{{man}}</p>
</article>
<article class='MP4'>
 <ul>
             <li>1125</li>
             <li>1126</li>
             <li>1127</li>
             <li>1128</li>
             <li>1129</li>
         </ul>
</article>
<article class='MP5'>
    <ul>
        <li v-for='M in MP' :key='M.id'>
            <ul class='mp5'>
                <li><p class='font20'>{{M.star}}</p>
                <p class='color999'>{{M.end}}</p></li>
                <li><p>{{M.yuyan}}</p>
                <p class='color999'>{{M.ting}}</p></li>
                <li>
                    <p class='red'>{{M.pj}}</p>
                    <p><span class='zhekouka'></span> </p>
                    <p class='color999'></p>
                </li>
                <li>
                    <span class='Goupiao' @click='SHOP(M.id)'></span>
                </li>
            </ul>
        </li>
    </ul>
</article>
<hr class='hr'/>
<article class='MP6'>
    <p></p>
    <dl v-for='XC in xiaochi' :key='XC.id' class='XC'>
        <dt><img :src="XC.img" /></dt>
        <dd>{{XC.msg}}</dd>
        <dd>{{XC.yishou}}</dd>
           <dd>
               <div>{{XC.qian}}</div>
           <div><span></span></div></dd>
       
    </dl>
</article>
<hr class='hr'/>
<hr class='hr'/>
    </div>

</template>
<script>
import Axios from 'axios'

    export default {
     
 data(){
     return{
         MPMsg:[],
         movieListTwo:[],
         TWO:[],
         title:'',
         pf:'9.2',
         sj:'108',
         jq:'',
         man:',',
         MP:[],
         xiaochi:[],
         id:[],
     }
 },methods:{
       mp2(two){
          this.TWO=two;
          this.title=this.TWO.title;
          this.pf=this.TWO.pingfen;
          this.sj=this.TWO.sj;
          this.jq=this.TWO.jq;
          this.man=this.TWO.man;
          this.id=two.id;
          console.log(this.id);
       },
       SHOP(XD){
           let id=this.id;
           this.$router.push('/xiadan'+XD);

         
       }
 },
 mounted(){
   let url='./static/data/data.json';
   let url2='./static/data/movieListTwo.json';
   let url3='./static/data/movieMP.json';
   let url4='./static/data/xiaochi.json'
   let MP4li=document.getElementsByClassName('MP4')[0].getElementsByTagName('li');

   Axios.get(url).then(res=>{
      let list=res.data.list;
      for(let msg in list){
          if(list[msg].id==this.$route.params.maiPiao){
              this.MPMsg=list[msg];
          }
      }
   }).catch(()=>{
       alert('');
   })

     Axios.get(url2).then(res=>{
    let list=res.data.movieListTwo;
   this.movieListTwo=list;
   }).catch(()=>{
       alert('');
   })

    Axios.get(url3).then(res=>{
    let list=res.data.maipiao;
   this.MP=list;
   }).catch(()=>{
       alert('');
   });
     Axios.get(url4).then(res=>{
    let list=res.data.xiaochi;
   this.xiaochi=list;
   console.log('xiaochi',this.xiaochi);
   }).catch(()=>{
       alert('');
   });
       for(let i=0;i<MP4li.length;i++){
           MP4li[i].onclick=function(){
               for(let j=0;j<MP4li.length;j++){
                   MP4li[j].style.color='#666';
                   MP4li[j].style.borderBottom='none';
               }
                MP4li[i].style.color='#F03D37';
                   MP4li[i].style.borderBottom='3px solid #F03D37';
           }
       }
 }
    }
</script>
<style>
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.tou{
    height:1rem;
    line-height: 1rem;
    color:#fff;
    background: #F04842;
    position:relative;
}
.tou .p{
    margin:0 auto;
    white-space: nowrap;
    overflow: hidden;
    text-overflow:ellipsis;
}
.tou span{
    position:absolute;
    left:3%;
    top:0;
    font-size:0.7rem;
    font-family: '';
  
}
.tou span a{
color:#fff;
}
.MP1{
    width:100%;
    padding:0 3%;
    margin:20px 0;
    overflow:hidden;
}
.MP1 dl{
    width:80%;
float:left;
}
.MP1 dl dd{
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.MP1 div{
float:right;
width:20%;
border-left:1px solid #D8D8D8;
text-align: center;
padding-top:10px;
height:40px;
position:relative;
top:5px;
}
.MP1 div img{
    width:20px;

}
.MP2{
    background: #7A8189;
    height:160px;
    width:100%;
    overflow: auto;
    padding:13px 4%;
}
.MP2::-webkit-scrollbar{
    display:none;
}
.mp2{
    width:400%;
    overflow: hidden;
}
.mp2 dl{
    width:8%;
    margin-right:2%;
    float:left;
    position:relative;
}
.mp2 dl img{
    width:100%;
    height:136px;
}
.mp2 dl dd{
    width:100%;
    position:absolute;
    bottom:2px;
    color:#F9D80D;
    text-align: center;
}
.MP3 p:nth-of-type(1){
text-align: center;
font-weight:bold;
font-size:16px;
}
.MP3{
    padding:15px 0;
    border-bottom:1px solid #F0F0F0;
}
.MP3 p:nth-of-type(1) span{
     color:orangered;

}
.MP3 p:nth-of-type(2){
    text-align: center;
    color:#666;
}
.MP4{
    overflow: auto;
    width:100%;
    border-bottom:1px solid #F0F0F0;
}
.MP4::-webkit-scrollbar{
    display:none;
}
.MP4 ul{
    width:200%;
    overflow: hidden;
}
.MP4 ul li{
    width:20%;
    text-align: center;
    float:left;
    height:1rem;
    line-height: 1rem;
    color:#666;
}
.MP4 ul li:nth-of-type(1){
color:#F03D37;
border-bottom:3px solid #F03D37;
}
.M ul{
width:100%;
display:flex;
justify-content: space-evenly;
}.font20{
    font-size:20px;
}
.color999{
    color:#999;
    font-size:12px;
}
.mp5{
    border-bottom:1px solid #f8f8f8;
    padding:10px 0;
     margin-top:5px;
     display:flex;
     justify-content: space-evenly;
}
.mp5 li:nth-of-type(2){
position:relative;
top:5px;
}
.mp5 li:nth-of-type(3){
position:relative;
top:2px;
}


.red{
    color:red;
    font-size:18px;
}
.zhekouka{
    color:#fff;
    background: #FF9900;
    font-size:12px;
}
.Goupiao{
  color: #fff;
   background: #F03D37;
   padding:5px 10px;
   border-radius:5px;
   position:relative;
   top:25px;
}
.hr{
    height:10px;
    background: #F0F0F0;
    border:none;
}
.MP6 p{
 border-bottom:1px solid #f8f8f8;
 padding:7px 3%;
}
.XC{
    overflow: hidden;
    width:100%;
    padding:10px 3%;
border-bottom:1px solid #F0F0F0;
}
.XC dt{
    float:left;
    width:30%;
}
.XC dt img{
    width:100%;
}
.XC dd{
    float:right;
    width:70%;
    padding-left:2%;
    position:relative;
    top:10px;
}
.XC dd:nth-of-type(1){
    color:#666;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
}
.XC dd:nth-of-type(2){
color:#999;
font-size:12px;
text-align: right;
}
.XC dd:nth-of-type(3){
    overflow: hidden;
}
.XC dd:nth-of-type(3) div:nth-of-type(1){
    float:left;
      color:red;
  font-size:18px;
  width:30%;
}

.XC dd:nth-of-type(3) div:nth-of-type(2){
    float:right;
      width:70%;
      text-align:right;
}
.XC dd:nth-of-type(3) div:nth-of-type(2) span{
       background: #F03D37;
       padding:6px 7px;
       color:#fff;
       border-radius:5px;
       font-size:12px;
       position:relative;
       top:2px;
}
@media(min-width:375px){
    .MP2{
    height:200px;
    
}
.mp2 dl img{
    height:170px;
}
.XC dd{
    top:15px;
}
}
@media(min-width:414px){
    .MP2{
    height:210px;
    
}
.mp2 dl img{
    height:185px;
}
.XC dd{
    top:20px;
}
}
</style>


