<template>
    <div>
          <div class='tou'>
  <span @click='lt(movieMsg.id)' style="font-family: '';font-size:0.7rem;">&lt; </span>
<p class='p' style='font-size:16px; text-align: center;  width:100%;'>{{movieMsg.nm}}</p>
   </div>

     <article id='Msga1'>
         <img :src='movieMsg.bg' class='bg'>
     <div class='a1one'>
         <div class='a1oneerzi'>
         <img :src='movieMsg.img' class='titleimg'/>
     <span><img src="./static/img/bofang.png" alt=""></span>
     </div>
     </div>
     <div class='a1two'>
         
         <ul>
             <li>{{movieMsg.nm}}</li>
             <li class='pingfen'>{{movieMsg.sc}}</li>
             <li class='font14'>{{movieMsg.ver}}</li>
             <li class='font14'>{{movieMsg.cat}}</li>
             <li class='font14'>{{movieMsg.data}}</li>
             <li class='font14'>{{movieMsg.rt}}</li>
         </ul>
     </div>
     </article>

     <article id="Msga2">
     <p @click='gouPiao(movieMsg.id)'></p>
     <div class='xiangqing'>{{movieMsg.xiangqing}}
       <div @click='msgzhankai()'> <img src="./static/img/sanjiaodown.png" alt="" id='msgzhankai'></div>
     </div>
      </article>
      <hr class='hr'/>
      <article id="Msga3">
          <div class='Msga3'>
        <dl v-for='yy in movieMsg.man' :key='yy.id'>
            <dt><img :src='yy.img'/></dt>
            <dd>{{yy.name}}</dd>
            <dd class='color666'>{{yy.shiyan}}</dd>
        </dl>
        </div>
      </article>
      <article id='Msga4'></article>
       <hr class='hr'/>
       <article id='Msga5'>
           <h4></h4>
           <dl>
               <dt><img :src="movieMsg.zx"/></dt>
               <dd>{{movieMsg.zxxx}}</dd>
           </dl>
       </article>
          <hr class='hr'/>
          <article id="Msga6">
              <h4></h4>
              <p>~</p>
          </article>
        
            <article id='Msga7'>
                <p></p>
            </article>
    </div>
</template>
<script>
import Axios from 'axios'
    export default {
         data(){
             return{
                movieMsg:[],
                zhankai:false,
             }
         },
         methods:{
            lt(movieId){
                this.$router.push('/moviedetail'+movieId);
            },
            gouPiao(movieId){
                this.$router.push('/moviedetail'+movieId);
            },
            msgzhankai(){
             let oMsgzhankai=document.getElementById('msgzhankai');
             let xiangqing=document.getElementsByClassName('xiangqing')[0]; 
              this.zhankai=!this.zhankai;
              console.log(this.zhankai);
              if(this.zhankai==true){
                  oMsgzhankai.src='./static/img/sanjiaoup.png';
                  xiangqing.style.height='180px';
              }else{
                   oMsgzhankai.src='./static/img/sanjiaodown.png';
                   xiangqing.style.height='100px';
              }
            }
         },
         mounted(){
            let url='./static/data/movieDetailmsg.json';
               Axios.get(url).then(res=>{
         let list=res.data.movieMsg;
        for(let msg in list){
         if(list[msg].id==this.$route.params.Msg){
         this.movieMsg=list[msg];
       console.log('movieMsg',this.movieMsg);
            
         }
        }
          }).catch(()=>{
              alert('');
          });
        
        }
    }
</script>
<style>
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.tou{
    height:1rem;
    line-height: 1rem;
    color:#fff;
    background: #F04842;
    position:relative;
}
.tou .p{
    margin:0 auto;
    white-space: nowrap;
    overflow: hidden;
    text-overflow:ellipsis;
}
.tou span{
    position:absolute;
    left:3%;
    top:0;
    font-size:0.7rem;
    font-family: '';
  
}
.tou span a{
color:#fff;
}
#Msga1{
    width:100%;
    height:180px;
    overflow: hidden;
    position:relative;
}
.bg{
    width:100%;
    position:absolute;
    top:0;
    left:0;
    z-index:1;
}
.a1one{
  position:absolute;
  top:15px;
  left:3%;
    width:35%;
    z-index:7777;
}

.a1one .a1oneerzi{
    position:relative;
}
.a1one .a1oneerzi span{
    position:absolute;
    right:0;
    bottom:0;
}
.a1two{
  position:absolute;
  top:15px;
  right:3%;
    width:56%;
     z-index:9999;
}
.a1one .titleimg{
    width:100%;
}
.a1two li{
    color:#f8f8f8;
}
.a1two li:nth-of-type(1){
font-size:18px;
    color:#fff;
}
.a1two .pingfen{
    color:#FF9900;
    font-weight:bold;
    font-size:18px;
}
.font14{
    font-size:12px;
}
#Msga2 p{
   width:92%;
   height:0.8rem;
   line-height: 0.8rem;
   border-radius:5px;
   background: #F03D37;
   margin:15px auto;
   color:#fff;
   font-size:16px;
   text-align: center;
}
#Msga2 .xiangqing{
     width:92%;
      margin:10px auto;
height:100px;
overflow: hidden;
position:relative;
}
#Msga2 .xiangqing div{
    position:absolute;
    bottom:0;
    left:0;
    width:100%;
    height:25px;
    padding-top:6px;
    background: #fff;
    text-align: center;
}
.hr{
height:10px;
background: #F4F4F4;
border:0;
border:none;
border-top:1px solid #E5E5E5;
border-bottom:1px solid #E5E5E5;
}
.color666{
color:#666;
}
#Msga3{
    padding-left:3%;
    width:100%;
    overflow: auto;
    margin-top:15px;
}
#Msga3::-webkit-scrollbar{
    display:none;
}
.Msga3{
    overflow: hidden;
    width:200%;
} 
.Msga3 dl{
    width:9%;
    float:left;
    margin-right:1%;
}
.Msga3 dl img{
    width:100%;
}
.Msga3 dl dd{
    text-align: center;
}
#Msga4{
    height:1rem;
    line-height: 1rem;
    border-top:1px solid #E5E5E5;
    padding-left:3%;
    color:#666;
}
#Msga5,#Msga6{
    padding:10px 3%;
}
#Msga5 dl{
    overflow: hidden;
}
#Msga5 dl dt{
    float:left;
    width:30%;
}
#Msga5 dl dd{
    float:right;
    width:67%;
}
#Msga6 p{
    text-align: center;
}
#Msga7{
background: #F4F4F4;
height:70px;
padding-top:15px;
}
#Msga7 p{
    line-height: 0.8rem;
    height:0.8rem;
     background: #F03D37;
     color:#fff;
     text-align: center;
     width:92%;
     margin:0 auto;
     
}
@media (min-width:375px){
  .font14{
    font-size:14px;
}  
#Msga1{
    height:210px;
}
.a1two li:nth-of-type(1){
font-size:20px;
}
.a1two .pingfen{
    font-size:20px;
}
}
@media(min-width:414px){
      .font14{
    font-size:16px;
}
#Msga1{
    height:230px;
}
.a1two li:nth-of-type(1){
font-size:22px;
}
.a1two .pingfen{
    font-size:22px;
}
}
</style>



