<template>
   <div>
       <div class='XDmb'>
           <div class='xdmb'>
             <div>
               <p></p>
               <p></p>
               <p @click='$router.go(-1)'></p>
               </div>
           </div>
       </div>
           <div class='tou'>
  <span @click='$router.go(-1)' style="font-size:0.7rem;font-family: '';">&lt; </span>
<p style="  width:100%;font-size:16px;  text-align: center;"></p>
   </div>
     
     <div class='shijian'><p><span>{{fz}}:{{miao}}</span></p></div>
     <article class='XDA1'>
 <ul>
     <li>{{list.nm}}</li>
     <li>12:00</li>
     <li>()</li>
     <li> 6</li>
 </ul>
 <p><span></span><span></span></p>
 <p><span></span><span>166102833</span></p>
 <p><span></span><span>27</span></p>
     </article>
     <hr class='hr'/>
     <article class='MP6'>
    <p></p>
    <dl v-for='XC in xiaochi' :key='XC.id' class='XC'>
        <dt><img :src="XC.img" /></dt>
        <dd>{{XC.msg}}</dd>
        <dd>{{XC.yishou}}</dd>
           <dd>
               <div>{{XC.qian}}</div>
           <div><span></span></div></dd>
       
    </dl>
</article>

<article id='fter'>
    <div id='ft'>
<p class='zf'></p>
    </div>
</article>
   </div>
 
</template>
<script>
import Axios from 'axios'
    export default {
   data(){
       return{
           
         fz:14,
         miao:59,
        list:[],
         xiaochi:[],
       }
   },
   methods:{
  
   },mounted(){ 
        let url4='./static/data/xiaochi.json' 
        let url='./static/data/moviedata.json';
        let zf=document.getElementsByClassName('zf')[0];
       let timer=null;
       let OXDMB=document.getElementsByClassName('XDmb')[0];
       let oShijian=document.getElementsByClassName("shijian")[0];

     Axios.get(url).then(res=>{
       let list=res.data.data.movies; 
       for(let msg in list){
           if(list[msg].id==this.$route.params.XD){
             this.list=list[msg];
           }
       }
      
     }).catch(()=>{
         alert('');
     })
   Axios.get(url4).then(res=>{
    let list=res.data.xiaochi;
   this.xiaochi=list;
   console.log('xiaochi',this.xiaochi);
   }).catch(()=>{
       alert('');
   });
 
 let go=()=>{
 timer=setInterval(()=>{
  this.miao--;
if(this.miao==0){
    this.miao=15;
    this.fz--;
}
if(this.fz==0){
   if(this.miao==1){
 clearInterval(timer);
 console.log('');
 oShijian.innerHTML='';
 oShijian.style.background='#ccc';
 oShijian.style.color='#666';
 OXDMB.style.display='block';
} 
}
console.log(this.miao);
 
      },1000)
 }
if(this.fz!=0&&this.miao!=0){
    go();
}
 
   }

    }

</script>
<style>
    *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.XDmb{
    width:100%;
    height:100%;
background: rgba(0,0,0,0.5);
    position:fixed;
    top:0;
    left:0;
    z-index:7777;
    display:none;

}

.xdmb div{
height:150px;
width:90%;
margin:200px auto 0 auto;
background: #fff;
border-radius:10px;
position:relative;
padding-top:20px;
}
.xdmb div p{
    text-align: center;
    width:100%;
}
.xdmb div p:nth-of-type(1){
font-size:18px;
font-weight:bold;

}
.xdmb div p:nth-of-type(2){
color:#999;
margin-top:10px;
}
.xdmb div p:nth-of-type(3){
color:#3BA3D7;
position:absolute;
bottom:0;
height:1rem;
border-top:1px solid #F7F7F7;
line-height: 1rem;
}
.tou{
    height:1rem;
    line-height: 1rem;
    color:#fff;
    background: #F04842;
    position:relative;
    z-index:6666;
}
.tou p{
    width:100%;font-size:16px;  text-align: center;
  
    
    width:70%;
    margin:0 auto;
    white-space: nowrap;
    overflow: hidden;
    text-overflow:ellipsis;
}
.tou span{
    position:absolute;
    left:3%;
    top:0;
    
  
}
.tou span a{
color:#fff;
}
.shijian{
    background: #FEF3D9;
    color:#FF9900;
    text-align: center;
    padding:8px 0;
}
.XDA1{
    padding:0 3%;
    margin-top:10px;
}
.XDA1 ul li:nth-of-type(2){
color:red;
}
.XDA1 ul li:nth-of-type(1){
    font-size:16px;
}
.XDA1 p{
    overflow: hidden;
    line-height: 0.9rem;
    border-bottom:1px solid #f8f8f8;
}
.XDA1 p span:nth-of-type(1){
    float:left;
    color:#999;
}
.XDA1 p span:nth-of-type(2){
    float:right;
}
.XDA1 p:nth-of-type(3) span:nth-of-type(2){
    color:red;
    font-size:18px;
}
.hr{
    height:10px;
    background:#F0F0F0;
    border:0;border:none;
}
.MP6 p{
 border-bottom:1px solid #f8f8f8;
 padding:7px 3%;
}
.XC{
    overflow: hidden;
    width:100%;
    padding:10px 3%;
border-bottom:1px solid #F0F0F0;
}
.XC dt{
    float:left;
    width:30%;
}
.XC dt img{
    width:100%;
}
.XC dd{
    float:right;
    width:70%;
    padding-left:2%;
    position:relative;
    top:10px;
}
.XC dd:nth-of-type(1){
    color:#666;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
}
.XC dd:nth-of-type(2){
color:#999;
font-size:12px;
text-align: right;
}
.XC dd:nth-of-type(3){
    overflow: hidden;
}
.XC dd:nth-of-type(3) div:nth-of-type(1){
    float:left;
      color:red;
  font-size:18px;
  width:30%;
}

.XC dd:nth-of-type(3) div:nth-of-type(2){
    float:right;
      width:70%;
      text-align:right;
}
.XC dd:nth-of-type(3) div:nth-of-type(2) span{
       background: #F03D37;
       padding:6px 7px;
       color:#fff;
       border-radius:5px;
       font-size:12px;
       position:relative;
       top:2px;
}
#fter{
    width:100%;
    height:1.2rem;

}
#ft{
  width:100%;
  height:1.2rem;
  background: #fff;
    position:fixed;
  bottom:0;
  left:0;

}
#ft p{
    color:#fff;
    background:#FF9900;
    width:94%;
    margin:0 auto;
    height:0.8rem;
    line-height: 0.8rem;
    text-align: center;
    border-radius:10px;
    margin-top:0.2rem;
}

@keyframes dh {
    0%{opacity:0;}
   50%{opacity:1;}
   100%{
       opacity:0;
       display:none;
   }
}
@media (min-width:375px){
    .xdmb div{
        margin:250px auto 0 auto;
    }
}
@media (min-width:414px){
    .xdmb div{
        margin:280px auto 0 auto;
    }
}
@media (min-height:812px){
    .xdmb div{
        margin:310px auto 0 auto;
    }
}
</style>


