<template>
    <div>
      <div id='youDZ'>
          
        <p class='dd'></p>
        <div class='hot'>
          
            <span @click='city(hot.name)' v-for='hot in hot' :key='hot.id'>{{hot.name}}</span>
        
        </div>
        <div class=youDZ>
             
        <div class='chengshi'>
            <ul>
                <li @click='city(chengshi.n)' v-for='chengshi in chengshi' :key='chengshi.id' class='cheng'>
                 {{chengshi.n}}
                 
                </li>
            </ul>
           
        </div>
        </div>
      </div>
        <div id='brk'>
   <article id="nav">
         <ul>
             <li class='margin-right' id='a1'>{{dd}}<img src="../../assets/img/sanjiao.png" class='ii'/></li>
             <li class='margin-right'><router-link to="#" id='a2'></router-link> </li>
             <li><router-link to="#" id='a3'></router-link> </li>
             <li><router-link to="#"><img src="../../assets/img/fdj.jpg" alt=""> </router-link> </li>
         </ul>
       </article>
       </div>

<div id='u1'>
      <ul class='move-list'>
           <li  v-for='movie in movieList' :key='movie.id' class='movie'>
<div class='movie-img' @click='goDetail(movie.id)'>
    <img :src='movie.img'>
</div>
<div class='msg'>
<ul class='movie-info' @click='goDetail(movie.id)'>
      <li >{{movie.nm}}</li>
                 <li class='color666' >{{movie.ver}}</li>
                 <li class='color666'>{{movie.star}}</li>
                 <li class='color666'>{{movie.showInfo}}</li>
</ul>
<div class='yd'><router-link :to="'/xiadan'+movie.id"><span ></span>   </router-link> </div>
</div>
           </li>
       </ul>
</div>
<div id='u2'>

    <p></p>
<article id="a1">
<div class='dl'>
<dl v-for='movieTwo in movieListTwo' :key='movieTwo.id'>
    <dt><img :src='movieTwo.img'/> 
    <span>{{movieTwo.want}}</span>
    </dt>
    <dd>{{movieTwo.title}}</dd>
    <dd>{{movieTwo.data}}</dd>
    <dd   class='aixin' @click='Img(movieTwo.id)' >
        <img src='../../assets/img/aixin1.png' class='aixinimg' v-show='mm' />
         <img src='../../assets/img/aixin2.png' class='aixinimg' v-show="nn" />
         </dd>
    <dd class='like'>~</dd>
</dl>
</div>
</article>
<article id="a2">
   <div class='list'>
 <p>1127 </p>
       <ul>
           <li class='movie'>
       <div class='movie-img'>
           <img src="https://p1.meituan.net/128.180/movie/f18a1a371e38920840b315f51e846e271016796.jpg" alt=""> </div>        
         <div class='msg'>
          <ul class='movie-info'>
              <li></li>
              <li><span>2874</span> </li>
              <li>,,</li>
              <li>2018-11-27</li>
               </ul>
          <div class='yd shop'><router-link to="#" style='background:#3C9FE6'></router-link>  </div>    
         </div>
           </li>
       </ul>
        <p>1130 </p>
       <ul>
           <li class='movie'  v-for="movieTwo2 in movieListTwo2" :key='movieTwo2.id'>
       <div class='movie-img'><img :src='movieTwo2.img'/> </div>        
         <div class='msg'>
          <ul class='movie-info'>
              <li>{{movieTwo2.title}}</li>
              <li><span>{{movieTwo2.want}}</span> </li>
              <li>{{movieTwo2.man}}</li>
              <li>{{movieTwo2.data}}</li>
               </ul>
          <div class='yd'><router-link to="#" ></router-link>  </div>    
       
         </div>
           </li>
       </ul>
      
   </div>

</article>

</div>

    </div>
     
</template>
<script>
import Axios from 'axios'
    export default {
        data(){
        return{
         movieList:[],
         movieListTwo:[],
         movieListTwo2:[],
         loadingShow:false,
         mm:true,nn:false,
         hot:[],
         chengshi:[],
         dd:'',
    
        }
    },
    methods:{

loaDate(){
 
         let url1='./static/data/moviedata.json';
         let url2='./static/data/movieListTwo.json';
          let url3='./static/data/movieListTwo2.json';
       Axios.get(url1).then(res=>{
           let list=res.data.data.movies; 
          
          this.movieList=list;
       }).catch(
           ()=>{
               alert('')
           }
       );
       Axios.get(url2).then(res2=>{
      let list2=res2.data.movieListTwo;
     this.movieListTwo=list2;
       }).catch(()=>{
           alert('');
       });
         Axios.get(url3).then(res3=>{
      let list3=res3.data.movieListTwo2;
      let data=list3.slice(this.movieListTwo2.length,this.movieListTwo2.length+5);
      this.movieListTwo2=this.movieListTwo2.concat(data);
     
     

     console.log(this.movieListTwo2[0]);
       }).catch(()=>{
           alert('');
       })   
},
goDetail(movieId){
         this.$router.push('/moviedetail'+movieId);
     },
     dis(){
     let oA2=document.getElementById('a2');
     let oA3=document.getElementById('a3');
     let oU1=document.getElementById('u1');
     let oU2=document.getElementById('u2');
     oA2.style.color='#E54847';
     oA2.style.borderBottom='3px solid #E54847';
     oA2.onclick=function(){
          oA2.style.color='#E54847';
     oA2.style.borderBottom='3px solid #E54847';
          oA3.style.color='';
     oA3.style.borderBottom='';
      oU1.style.display='block';
      oU2.style.display='none';
     }
    oA3.onclick=function(){
             oA2.style.color='';
     oA2.style.borderBottom='';
          oA3.style.color='#E54847';
     oA3.style.borderBottom='3px solid #E54847';
     oU1.style.display='none';
     oU2.style.display='block';
    }
     },
     Img(movieTwoId){
         this.mm=!this.mm;
         this.nn=!this.nn;
         console.log(movieTwoId);
         console.log('mm',this.mm,'nn',this.nn);
   //let oAixin=document.getElementsByClassName('aixin');
  // let oImg=document.getElementsByClassName('aixinimg');
   let oLike=document.getElementsByClassName('like');
   //oImg[movieTwoId].src="../../assets/img/aixin2.png";
   oLike[movieTwoId].style.animation='like 1s 1';
 
     },oA1(){
         let oA1=document.getElementById('a1');
         let oYouDZ=document.getElementById('youDZ');
         let body=document.getElementsByTagName('body');
         oA1.onclick=function(){
            oYouDZ.style.display='block';
            oYouDZ.style.position='fixed';
            oYouDZ.style.top='0';
            oYouDZ.style.left='0';
    // body

         }
     },Dingwei(){
        let url='./static/data/city.json';
        Axios.get(url).then(res=>{
            let hot=res.data.hot;
            let chengshi=res.data.city;
            this.hot=hot;   
            this.chengshi=chengshi;
            console.log('this.hot',this.hot);
              console.log('this.chengshi',this.chengshi);
        }).catch(()=>{
            alert('city');
        })
     },city(chengshiN){
       this.dd=chengshiN;
       console.log(this.dd);
        let oYouDZ=document.getElementById('youDZ');
          oYouDZ.style.display='none';
            oYouDZ.style.position='static';
          
     }
    
 
      },
        mounted(){
        this.loaDate();
  window.onscroll=()=>{
   let clientHeight= document.documentElement.clientHeight;//
   let scrollTop=document.documentElement.scrollTop;//
   let scrollHeight=document.documentElement.scrollHeight;//
   if(clientHeight+scrollTop==scrollHeight){
   this.loadingShow=true;
   if(!this.tip){
       this.loaDate();
   }else{
       this.loadingShow=false;
   }

   }
   
};
   this.dis();
   this.oA1();
   this.Dingwei();

    },
        }
    
</script>
<style>
#youDZ{

  width:100%;
  height:100%;
  z-index:9999;
 background: #F5F5F5;
 display:none;
 overflow: hidden;
}
.youDZ{
width:100%;
  height:320px;
  overflow: auto;
}
.youDZ::-webkit-scrollbar{
    display:none;
}
.chengshi{
    overflow:hidden;
}
.cheng{
    width:88%;
    margin:0 auto;
    border-bottom:1px solid #C8C7CC;
    line-height: 0.8rem;
    height:0.8rem;
    color:#333;
}
.baorong{
    overflow: hidden;
}
.dd{
   background: #EBEBEB;
   padding:2px 3%;
   color:#333;
}
.hot{
    margin-top:20px;
    padding:0 6%;
}
.hot span{
    display:inline-block;
    padding:5px 25px;
    border:1px solid #E6E6E6;
    background: #fff;
    margin:0 10px 10px 0;

}
#brk{
    height: 1rem;
    width:100%;
}
#nav{
    background: #fff;
    position:fixed;
    top:1rem;
    left:0;
  width:100%;   
    height: 1rem;
    border-bottom:1px solid #f1f1f1;
      z-index:7777;
}
#nav ul{
      width:90%;
       margin:0 auto;
    line-height: 1rem;
    overflow: hidden;
}
#nav ul li{
  float:left;
    color:#666;
}
#nav #a1{
   width:70px;
   height:1rem;
   overflow: hidden;
   white-space: nowrap;
   text-overflow: ellipsis;
}

#a1 .ii{
    width:10px;
    height:5px;
    position:relative;
    top:22px;
    left:3px;
}
.margin-right{
    margin-right:8%;
}
#nav ul li:last-child{
    width:14%;
    float:right;
    text-align: right;

}
#nav ul li:last-child img{
    transform:scale(0.8);
    position:relative;
    top:10px;
}
#nav ul li a{
    display:block;
    color:#666;
    outline: none;
    border-bottom:3px solid rgba(255,0,0,0);
}
#nav ul li:nth-of-type(2) a:hover{
font-weight: bold;
}
#nav ul li:nth-of-type(3) a:hover{
font-weight: bold;
}
.move-list{
    width:90%;
    margin:0 auto;

}
.movie{
    overflow:hidden;
    margin-top:15px;
 
}

.movie-img{
    width:24%;
    float:left;
}
.movie-img img{
    width:100%;
}
.msg{
    width:72%;
        height:100px;
   border-bottom:1px solid #f1f1f1;
    float:right;
    overflow:hidden;
}
.movie-info{
   float:left;
    width:70%;
}
.movie-info li{
    width:100%;
    overflow: hidden;  
    white-space: nowrap;
    text-overflow: ellipsis;
}
.movie-info li:nth-of-type(1){
    font-weight: bold;
    font-size:16px;
}
.color666{
    color:#666;
    font-size:12px;
}
.yd{
    width:28%;
    float:right;
    height: 90px;
}
.yd a{
    width:70% ;  
margin:30px auto 0 auto;
padding:3px;
    background: #E54847;
    color:#fff;
    text-align: center;
    border-radius:5px;
    display:block;
    font-size:12px;
}
 .gif{
      width:100%;
      text-align: center;
  }
  .tip{
      margin-bottom:1rem;
      text-align:center;
  }

#u2{
    width:100%;
    display:none;

}
#u2 #a1{
    width:90%;
    overflow: auto;
    margin:0 auto;
    z-index:1;
}
#u2 #a2{
    width:100%;
    margin-top:10px;
    border-top:10px solid #f5f5f5;
}
#u2 #a1::-webkit-scrollbar{
    display: none;
}
#u2>p{
    width:90%;
    margin:10px auto;
    color:#666;
}
#u2 #a1 .dl{
    width:400%;
    overflow: hidden;
}
#u2 #a1 dl{
    width:8.5%;
    float:left;
    margin-right:1.5%;
    position: relative;
}
#u2 #a1 dl img{
    width:100%;
    height:130px;

}
#u2 #a1 dl dt{
    position:relative;
}
#u2 #a1 dl dt span{
    position:absolute;
    bottom:0;
    color:#F2A901;
    font-size:12px;
    font-weight:bold;
display:block;
width:100%;
text-align: center;
}
#u2 #a1 dl dd:nth-of-type(1){
    font-weight:bold;
}
#u2 #a1 dl dd:nth-of-type(2){
    color:#666;
    font-size:12px;
}
#u2 #a1 dl dd:nth-of-type(3){
    position:absolute;
    top:0;
    left:0;
    width:28px;
    height:28px;
   background: rgba(0,0,0,0.4);
    border-radius:0 0 10px 0;
    text-align: center;
}
#u2 #a1 dl dd:nth-of-type(3) img{
    width:50%;
    height:50%;
    margin-top:5px;
    opacity:0.9;
}
#u2 #a1 dl dd:nth-of-type(4){
    width:100%; 
    position:absolute;
   top:40px;
   color:#fff;
   text-align: center;
   opacity:0;

}
@keyframes like {
    0%{
        opacity: 0;
    }
    30%{
        opacity: 1; 
    }
    60%{
         opacity: 1;
    }
    100%{
 opacity: 0;
    }
}
#u2 #a2 .list{
    width:90%;
    margin:0 auto;
    margin-bottom:1rem;
}
#u2 #a2 .yd a{
 background: #FAAF00;
}
#u2 #a2 .movie{
    margin-bottom:10px;
}
#u2 #a2 p{
    margin:5px 0;
}
#u2 #a2 .movie-info li{
    color:#666;
}
#u2 #a2 .movie-info li:nth-of-type(1){color:#000;}
#u2 #a2 .movie-info li:nth-of-type(2) span{
    color:#FAAF00;
    font-weight:bold;
}
@media (min-width:375px){
    .movie-info li:nth-of-type(1){
    font-size:18px;
}
    .color666{
    font-size:14px;
}
.msg{
      height:120px;
}
.yd{
    height: 110px;
}
.yd a{
margin:42px auto 0 auto;
}
#u2 #a1 dl img{
    height:150px;
}

#a1 .ii{
    top:25px;
    left:5px;
}
.youDZ{
  height:420px;
}
}
@media (min-width:414px){
    .movie-img{
    width:22%;
}
.msg{
    padding-top:3px;
    width:74%;
}
#u2 #a1 dl img{
    height:160px;
}
#a1 .ii{
    top:28px;
    left:5px;
}
.youDZ{
  height:540px;
}
}
@media (min-height:812px){
.youDZ{
  height:570px;
}
}


</style>


