#dl2 p:nth-of-type(1) span:nth-of-type(1){
       border:1px solid red;
           background: #FCF0F0;
            color:red;
}
.one:nth-of-type(3) div{
    overflow: hidden;
    border-top:1px solid #E5E5E5;
    background: #FAFAFA;
    padding:15px 5px;
    margin-top:20px;

}
.one:nth-of-type(3) div span{
    height:40px;
    width:66px;
 line-height: 40px;
 text-align: center;
 border-radius:4px;
}
#l{
    float:left;
    border:1px solid #ccc;
    color:#666;
}
#r{
    float:right;
    background:#F03D37;
    color:#fff;
}
@media(min-width:375px){
    #a1{
        height:210px;
    }
    #a1 .movie-img{
   top:15px;
}
#a1 .right{
   top:15px;
   height:180px;
   overflow: hidden;
}
#a1 .list li{
    font-size:16px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
}
#a1 .list li:nth-of-type(1){
    font-size:20px;
}
.gt{
    line-height: 180px;
    font-size:22px;
}
#a1 .movie-img{
   height:180px;

}
}
@media(min-width:414px){
    #a1{
        height:230px;
    }
    #a1 .movie-img{
   top:15px;
}
#a1 .right{
   top:15px;
   height:200px;
   overflow: hidden;
}
#a1 .list li{
    font-size:18px;
}
#a1 .list li:nth-of-type(1){
    font-size:22px;
}
.gt{
    line-height: 200px;
    font-size:22px;
}
#a1 .movie-img{
   height:200px;

}
}
</style>



