<template>
   <div class='footer'>
    
<ul class='ul'>
    <li @click='img1()'><router-link to="./movielist">
        <div>
        <img src="../../assets/img/footer1.jpg" alt="" class='IMG'>
        </div>
        <div class='DIV'>
         
         </div>
         </router-link> 
         </li>

  <li @click='img2()'><router-link to="./yingyuan">
        <div>
        <img src="../../assets/img/footer2.jpg" alt=""  class='IMG'>
        </div>
        <div class='DIV'>
         
         </div>
         </router-link> 
         </li>
         <li @click='img3()'><router-link to="/me">
        <div>
        <img src="../../assets/img/footer3.jpg" alt=""  class='IMG'>
        </div>
        <div class='DIV'>
         
         </div>
         </router-link> 
         </li>
</ul>
   </div>
</template>
<script>
    export default {
        data(){
            return{}
        },
        methods: {
            img1(){
         let ODIV=document.getElementsByClassName('DIV');
            let OIMG=document.getElementsByClassName('IMG');
            OIMG[0].src='./static/img/footer1.jpg';
            OIMG[1].src='./static/img/footer2.jpg';
            OIMG[2].src='./static/img/footer3.jpg';
            ODIV[0].style.color='#E54847';
            ODIV[1].style.color='#666';
            ODIV[2].style.color='#666';
            },img2(){
                 let ODIV=document.getElementsByClassName('DIV');
                 let OIMG=document.getElementsByClassName('IMG');
            OIMG[0].src='./static/img/footer1_1.jpg';
            OIMG[1].src='./static/img/footer2_1.jpg';
            OIMG[2].src='./static/img/footer3.jpg';
             ODIV[1].style.color='#E54847';
            ODIV[0].style.color='#666';
            ODIV[2].style.color='#666';
            },img3(){
                 let ODIV=document.getElementsByClassName('DIV');
                 let OIMG=document.getElementsByClassName('IMG');
            OIMG[0].src='./static/img/footer1_1.jpg';
            OIMG[1].src='./static/img/footer2.jpg';
            OIMG[2].src='./static/img/footer3_1.jpg';
             ODIV[2].style.color='#E54847';
            ODIV[0].style.color='#666';
            ODIV[1].style.color='#666';
            }
        },
    }

</script>
<style>
.footer{
    width:100%;
    height:1rem;
    position:fixed;
    bottom:0;
    border-top:1px solid #E2E2E2;
}
.footer .ul{
     width:100%;
    height:1rem;
 background: #fff;
overflow: hidden;
  z-index:9999;

}
.footer .ul li img{
    transform:scale(0.9);
}
.footer .ul li{
    padding:2px 0;
    text-align: center;
    width:33.33%;
    float:left;
}

.footer .ul li a{
    color:#666;
font-size:0.2rem;
text-align: center;
outline: none;
}
.footer .ul li:nth-of-type(1) a{
    color:#E54847;
}
</style>


